* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 1920px;
  height: 100vh;
  font-family: "Montserrat-Regular", "Arial", sans-serif; }

h2,
h3,
p,
ul {
  margin: 0;
  padding: 0; }

nav {
  margin: 0;
  padding: 0; }

.page {
  display: grid;
  grid-template-areas: "menu order info";
  grid-template-columns: 260px auto 360px; }

.order {
  margin: 60px 60px 0;
  grid-area: order;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-column-gap: 30px;
  grid-template-rows: repeat(4, auto); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden; }

@font-face {
  font-family: "Montserrat-Regular";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff.woff") format("woff"); }

@font-face {
  font-family: "Montserrat-Bold";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"); }

.head {
  margin: 0 auto;
  background-color: #ecf2ff; }

.head__wrap {
  margin: 0 60px;
  padding: 20px 0;
  display: grid;
  grid-template-columns: repeat(12, 1fr); }

.head__nav {
  margin-top: 10px;
  grid-column: 2 / span 6; }

.head__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  text-transform: uppercase; }

.head__link {
  position: relative;
  margin-right: 46px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat-Bold", "Arial, sans-serif";
  text-decoration: none;
  color: #5957db; }
  .head__link:not(.head__link--logo)::after {
    content: "";
    position: absolute;
    top: 3px;
    right: -20px;
    width: 6px;
    height: 10px;
    background: url("../img/breadcrumb_icon.svg") no-repeat; }
  .head__link.head__link--logo {
    margin-right: 60px; }
  .head__link.head__link--active::after {
    background: none; }

.head__link--active {
  color: #666666; }

.head__btn {
  margin: 0 0 0 auto;
  padding: 14px 50px 14px 24px;
  grid-column: 10 / span 3;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat-Bold", "Arial, sans-serif";
  text-transform: uppercase;
  color: #5957db;
  background-color: #ffffff;
  border: none;
  border-radius: 8px; }

.head__span {
  position: relative; }
  .head__span::after {
    content: "";
    position: absolute;
    top: 3px;
    right: -24px;
    width: 10px;
    height: 6px;
    background: url("../img/head-btn_icon.svg") no-repeat; }

.menu {
  padding: 20px 0 0 30px;
  grid-area: menu;
  display: grid;
  grid-template-rows: 1fr auto;
  background-color: #5957db; }

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.menu__item--active {
  background-color: #ecf2ff;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px; }

.menu__link {
  position: relative;
  padding: 23px 0 23px 60px;
  display: inline-block;
  font-size: 14px;
  line-height: 16px;
  font-family: "Montserrat-Bold", "Arial, sans-serif";
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase; }
  .menu__link::before {
    content: "";
    position: absolute;
    top: 20px;
    left: 12px;
    width: 18px;
    height: 22px; }

.menu__link--sessions::before {
  background: url("../img/sessions_icon.svg") no-repeat; }

.menu__link--trailers::before {
  background: url("../img/trailers_icon.svg") no-repeat; }

.menu__link--news::before {
  background: url("../img/news_icon.svg") no-repeat; }

.menu__link--about::before {
  background: url("../img/about_icon.svg") no-repeat; }

.menu__link--contacts::before {
  background: url("../img/contacts_icon.svg") no-repeat; }

.menu__link--active {
  color: #5957db; }
  .menu__link--active.menu__link--sessions::before {
    background: url("../img/sessions-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--trailers::before {
    background: url("../img/trailers-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--news::before {
    background: url("../img/news-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--about::before {
    background: url("../img/about-active_icon.svg") no-repeat; }
  .menu__link--active.menu__link--contacts::before {
    background: url("../img/contacts-active_icon.svg") no-repeat; }

.menu__link--profile {
  display: inline-block;
  margin-bottom: 45px;
  padding-left: 82px; }
  .menu__link--profile::before {
    top: 11px;
    width: 40px;
    height: 40px;
    background-color: #ffffff;
    background-image: url("../img/profile_icon.svg");
    background-repeat: no-repeat;
    background-size: 40px 30px;
    background-position: 0 10px;
    border-radius: 50%; }

.date {
  grid-column: 1 / span 11;
  grid-row: 1 / span 1;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-column-gap: 30px; }

.date__list {
  grid-column: 2 / span 9;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.date__item {
  width: 300px; }
  .date__item:not(:last-child) {
    margin-right: 30px; }

.date__btn {
  height: 120px;
  background-color: #ecf2ff;
  border: none;
  border-radius: 8px;
  color: #5957db; }

.date__btn--left {
  grid-column: 1 / span 1; }

.date__btn--content {
  width: 100%;
  padding: 30px; }

.date__btn--active {
  color: #ffffff;
  background-color: #43d000;
  -webkit-box-shadow: 0 8px 20px rgba(67, 208, 0, 0.6);
          box-shadow: 0 8px 20px rgba(67, 208, 0, 0.6); }

.date__btn--control {
  position: relative; }
  .date__btn--control::before {
    content: "";
    position: absolute;
    top: 45%;
    left: 45%;
    width: 10px;
    height: 20px;
    background: url("../img/arrow_icon.svg") no-repeat; }

.date__btn--right::before {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }

.date__head {
  font-size: 24px;
  line-height: 30px;
  font-family: "Montserrat-Bold", "Arial", sans-serif; }

.date__text {
  font-size: 16px;
  font-size: 20px; }

.time {
  margin-top: 60px;
  grid-column: 1 / span 11;
  grid-row: 2 / span 1;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-template-rows: repeat(2, 60px);
  grid-column-gap: 30px;
  grid-row-gap: 40px;
  background-image: url("../img/time-bg_icon.svg");
  background-repeat: repeat-x;
  background-position: center; }

.time__row {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 80px repeat(10, 1fr);
  grid-column-gap: 30px; }

.time__text {
  padding-top: 20px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat-Bold", "Arial", sans-serif;
  text-transform: uppercase;
  color: #666666; }

.time__list {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.time__item {
  width: 190px; }
  .time__item:not(:last-child) {
    margin-right: 30px; }

.time__btn {
  width: 100%;
  height: 60px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat-Bold", "Arial", sans-serif;
  color: #5957db;
  background-color: #ecf2ff;
  border: none;
  border-radius: 30px; }

.time__btn--active {
  color: #ffffff;
  background-color: #43d000;
  -webkit-box-shadow: 0 8px 20px rgba(67, 208, 0, 0.6);
          box-shadow: 0 8px 20px rgba(67, 208, 0, 0.6); }
